<template>
    <div class="globlgl  globlgl--custom">
        <div class="btns">
            <el-button class="float-l btn" @click="handleRecordConfAdd">添加</el-button>
            <el-button class="btn" @click="handleBatchDelete">批量删除</el-button>
        </div>
        <div class="tables">
        <el-table class="table" ref="multipleTable" :data="recordConfData" border tooltip-effect="dark" @selection-change="handleSelectionChange">
            <el-table-column type="selection" width="55"></el-table-column>
            <el-table-column prop="vlanId" label="租户号">
                <template slot-scope="scope">
                    <el-button @click="handlerecordConfCheck(scope.$index, scope.row)" type="text">{{scope.row.vlanId}}</el-button>
                </template>
            </el-table-column>
            <el-table-column prop="filePath" label="录音文件路径"></el-table-column>
            <el-table-column prop="fileFormat" label="录音格式"></el-table-column>
            <el-table-column prop="recordDirection" label="录音方向"></el-table-column>
            <el-table-column label="操作" width="120">
                <template slot-scope="scope">
                    <el-button type="text" size="small" @click="handleRecordConfDelete(scope.$index, scope.row)">删除</el-button>
                    <el-button type="text" size="small" @click="handleRecordConfEdit(scope.$index, scope.row)">编辑
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
        <!--添加录音配置-->
        <el-dialog title="添加录音配置" class="w-500" :visible.sync="dialogFormVisible" @close="handleDialogWithFormClose()" :close-on-click-modal="2==1">
            <el-form :model="recordConfForm" :rules="formRules" labelPosition="left" ref="recordConfForm">
                <el-form-item prop="vlanId" label="租户号" :label-width="formLabelWidth" v-if="this.$store.baseStore.getters.getUserInfo.managerLevel == '999'">
                    <el-select v-model.number="recordConfForm.vlanId" placeholder="请选择">
                        <el-option  v-for="item in allCompanyId" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select> 
                </el-form-item>
                <el-form-item prop="fileFormat" label="录音格式" :label-width="formLabelWidth">
                    <el-input v-model="recordConfForm.fileFormat" :maxlength="16" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item prop="filePath" label="录音文件路径" :label-width="formLabelWidth">
                    <el-input v-model="recordConfForm.filePath" :maxlength="255" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item prop="recordDirection" label="录音方向" :label-width="formLabelWidth">
                    <el-input v-model.number="recordConfForm.recordDirection" auto-complete="off"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisible = false">取 消</el-button>
            <el-button type="primary" @click="handleRecordConfSave('recordConfForm')">确 定</el-button>
            </div>
        </el-dialog>
        <!--编辑录音配置-->
        <el-dialog title="编辑录音配置" class="w-500" :visible.sync="dialogFormEdit" @close="handleDialogWithFormClose()" :close-on-click-modal="2==1">
            <el-form :model="recordConfEditForm" :rules="formRules" labelPosition="left" ref="recordConfEditForm">
                <el-form-item prop="vlanId" label="租户号" :label-width="formLabelWidth" v-if="this.$store.baseStore.getters.getUserInfo.managerLevel == '999'">
                    <el-select v-model.number="recordConfEditForm.vlanId" placeholder="请选择">
                        <el-option  v-for="item in allCompanyId" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select> 
                </el-form-item>
                <el-form-item prop="fileFormat" label="录音格式" :label-width="formLabelWidth">
                    <el-input v-model="recordConfEditForm.fileFormat" :maxlength="16" auto-complete="off"></el-input>
                    
                </el-form-item>
                <el-form-item prop="filePath" label="录音文件路径" :label-width="formLabelWidth">
                    <el-input v-model="recordConfEditForm.filePath" :maxlength="255" auto-complete="off"></el-input>
                    
                </el-form-item>
                <el-form-item prop="recordDirection" label="录音方向" :label-width="formLabelWidth">
                    <el-input v-model.number="recordConfEditForm.recordDirection" auto-complete="off"></el-input>
                    
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormEdit = false">取 消</el-button>
            <el-button type="primary" @click="handleRecordConfSave('recordConfEditForm')">确 定</el-button>
            </div>
        </el-dialog>
        <!--查看弹窗-->
        <el-dialog title="查看录音配置" class="w-500" :visible.sync="dialogFormCheck" :close-on-click-modal="2==1">
            <div class="item">录音格式: {{showCheck.fileFormat}}</div>
            <div class="item">录音文件路径: {{showCheck.filePath}}</div>
            <div class="item">录音方向: {{showCheck.recordDirection}}</div>
            <div class="item">租户号: {{showCheck.vlanId}}</div>
            <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormCheck = false">取 消</el-button>
            <el-button type="primary" @click="dialogFormCheck=false">确 定</el-button>
            </div>
        </el-dialog>
        </div>
        <div class="float-r Pagination" style="text-align: left;margin-top: 10px;">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-size="10" layout="total, prev, pager, next" :total="count">
        </el-pagination>
        </div>
    </div>
</template>

<script>

  import {
    recordConfList,
    saverecordConf,
    deleterecordConfByIds
  }
    from '@/api/getData';
  import ElTableTreeColumn from '../base/table-tree/table-column.js';
  export default {
    data() {
      var checkNumber = (rule, value, callback) => { //必须填而且必须是数字
            if (value == undefined || "" === value) {
                callback(new Error('此处不能为空'));
            }
            if (!Number.isInteger(value)) {
                callback(new Error('请输入数字值'));
            } else {
                callback();
            }
        };
        var checkNull = (rule, value, callback) => { //要么不填，要填必须是数字
            if (value == undefined || "" === value) {
                callback();
            }
            if (!Number.isInteger(value)) {
                callback(new Error('请输入数字值'));
            } else {
                callback();
            }
        };
        var checkDateVule = (rule, value, callback) => { //必须填入日期
            if (value == undefined || "" === value) {
                callback(new Error('请输入日期'));
            }else{
                 callback();
            }
          
        };
      return {
        //分页
        currentRow: null,
        limit: 10,
        count: 0,
        currentPage: 1,
        recordConfData: [],
        showCheck: {}, //查看对象
        dialogFormCheck: false, //查看弹窗

        multipleSelection: [],
        //弹窗
        dialogFormVisible: false,
        dialogFormEdit: false, //编辑弹窗
        recordConfForm: {
          vlanId: '',
          fileFormat: '',
          filePath: '',
          recordDirection: '',
        },
        recordConfEditForm: {
             vlanId: '',
             fileFormat: '',
             filePath: '',
             recordDirection: '',
        },
        formLabelWidth: '80px',
        formRules: {
          vlanId: [{required: true,  validator: checkNumber, trigger: 'blur'}],
           fileFormat: [{required: true,message: '请输入录音格式', trigger: 'blur'}],
           recordDirection: [{validator: checkNull, trigger: 'blur'}],
         
        },
        currentUserVlanId:this.$store.baseStore.getters.getUserInfo.vlanId,
      }
    },
    created() {
      this.getrecordConfList(); //加载数据
    },
    mounted(){
        var coms = this.$store.baseStore.getters.getAllCompanyInfo;
        if(coms == undefined || coms.length == 0){
            this.$store.baseStore.dispatch('getAllDeptId');
        }else{
            this.$data.allCompanyId= coms;
        }
    },
    computed: {
        allCompanyId:function() {
             return this.$store.baseStore.getters.getAllCompanyInfo;
        }
    },
    components: {
      ElTableTreeColumn
    },
    methods: {
      //获取分页 总条数
      async getrecordConfList() {
        try {
          let query = {
          }
          //分页
          const res = await recordConfList({
            pageindex:this.currentPage,
            pagesize:this.limit,
            query:JSON.stringify(query)
          });
          if (res.resultCode == '0') {
            this.recordConfData = [];
            //防止后台数据幅值，，前台页面只需要改动此处就好，还有一个好处 就是只选择我需要的数据
            this.showCheck = res.result.data;
            this.count=res.result.total;
            res.result.data.forEach(item => {
              const tableItem = {
                fileFormat:item.fileFormat,
                filePath:item.filePath,
                recordDirection:item.recordDirection,
                uuid:item.uuid,
                vlanId:item.vlanId,
              }
              this.recordConfData.push(tableItem)
            })
          } else {
            throw new Error(res.message)
          }
        } catch (err) {
          console.log('获取数据失败', err);
        }
      },
      handSaveParam(args){
          if(args.vlanId == undefined || args.vlanId === '')delete args.vlanId;
          if(args.recordDirection == undefined || args.recordDirection === '')delete args.recordDirection;
         return args;
      },
      handleSizeChange(val) {
      },
      handleCurrentChange(val) {
        this.currentPage = val;
        this.getrecordConfList();
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      handleRecordConfSearch() {
        this.getrecordConfList();
      },
      formatter(row, column) {
        return ' -- ' + row.name;
      },
      dataFormat(row, column) {
         var time =  row[column.property];
         if(time != undefined && time != ''){
            return this.$moment(time).format("YYYY-MM-DD HH:mm:ss");   
         }else{
            return '';
         }
      },
      //增加
      handleRecordConfAdd() {
        var tempVlanId = this.$data.currentUserVlanId;
        this.recordConfForm = {
          vlanId: tempVlanId,
          fileFormat: '',
          filePath: '',
          recordDirection: '',
        };
        this.dialogFormVisible = true;
      },
      //关闭弹框时重置表单
      handleDialogWithFormClose() {
        for (var prop in this.$refs) {
          if (this.$refs.hasOwnProperty(prop)) {
            if (this.$refs[prop].$vnode.tag == 'vue-component-48-ElForm') {
              this.$refs[prop].resetFields();
            }
          }
        }
      },
      //批量删除
      handleBatchDelete() {
        let that = this;
        if (this.multipleSelection.length === 0) {
          this.$alert('请至少选择一条记录', '提示', {
            confirmButtonText: '确定',
            callback: () => {
              return false;
            }
          });
        } else {
          this.$confirm('此操作将永久删除记录, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(async() => {
            try {
              let deletedIdArr = [];
              that.multipleSelection.forEach(s => {
                deletedIdArr.push(s.uuid)
              })
              let deletedIdsString = deletedIdArr.join(',');
              let data = {
                uuid: deletedIdsString
              }
              //此方法可以和后台协商一致，单条与批量删除使用同一个函数
              const result = await deleterecordConfByIds(data);
              if (result.resultCode == '0') {
                that.getrecordConfList();
                this.$message({
                  type: 'success',
                  message: '删除成功!'
                });
              }
            } catch (err) {
              console.log(err)
            }
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '已取消删除'
            });
          });
        }
      },
      //删除当前记录
      handleRecordConfDelete(index, row) {
        let that = this;
        this.$confirm('此操作将永久删除记录, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(async() => {
          try {
            let deletedIdsString = [row.uuid].join(',');
            let data = {
              uuid: deletedIdsString
            }
            //此方法可以和后台协商一致，单条与批量删除使用同一个函数
            const result = await deleterecordConfByIds(data);
            if (result.resultCode == '0') {
              that.getrecordConfList();
              this.$message({
                type: 'success',
                message: '删除成功!'
              });
            }
          } catch (err) {
            console.log(err)
          }
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      },
      //编辑
      handleRecordConfEdit(index, row) {
        if (typeof row !== 'undefined') {
            this.recordConfEditForm.vlanId = row.vlanId;
            this.recordConfEditForm.fileFormat = row.fileFormat;
            this.recordConfEditForm.filePath = row.filePath;
            this.recordConfEditForm.recordDirection = row.recordDirection;
            this.recordConfEditForm.uuid = row.uuid;
        }
        this.dialogFormEdit = true;
      },
      //查看
      handlerecordConfCheck(index, row) {
        this.dialogFormCheck = true;
        this.$data.showCheck = row;
      },
      //添加 编辑的 保存
      handleRecordConfSave(formName) {
        let that = this;
        this.$refs[formName].validate(async(valid) => {
          if (valid) {
            try {
              //数据拿出来，如果为空则去掉
              var args = this.$refs[formName].model;
              var params = this.handSaveParam(args);
              const result = await saverecordConf(params);
              if (result.resultCode == '0') {
                  if(formName == 'recordConfForm'){//添加
                        that.dialogFormVisible = false;
                  }else{
                    this.dialogFormEdit = false;
                  }
                  that.getrecordConfList();
                  this.$message({
                     type: 'success',
                     message: '录音配置保存成功'
                  });
              }else{
                  this.$message({
                    type: 'error',
                    message: '保存失败'
                  });
              }
            } catch (err) {
               this.$message({
                  type: 'error',
                  message: '保存失败'
               });
            }
          } else {
             this.$message({
                type: 'info',
                message: '请检查输入是否不合法'
             });
            return false;
          }
        });
      },
    }
  }

</script>
<style scope lang="less">

  .globlgl {
    padding: 33px 0 0 26px;
    box-sizing: border-box;
    .float-l {
      float: left;
    }
    .float-r {
      float: right;
    }
    .m-t-25 {
      margin-top: 25px;
    }
    //table 改组建
    .el-table {
      width: 100%;
      height: 100%;
      border: 0;
      &:before,
      &:after {
        display: none;
      }
      .el-table__body-wrapper {
        overflow: hidden;
      }
      th,
      td,
      .cell {
        border: 0px;
        background-color: initial;
        padding: 0px;
      }
      .cell {
        padding-left: 10px;
      }
      tr {
        border-bottom: 1px solid #e6e6e6;
      }
      thead tr {
        border-bottom: 2px solid #e6e6e6;
      }
    }
    .w-500 {
      .el-dialog {
        width: 500px;
        .el-dialog__body {
          max-height: 500px;
          overflow-y: auto;
          .item{
            height:40px;
            line-height:40px;
          }
        }
      }
    }
    .w-1000 {
      .el-dialog {
        width: 1000px;
        .el-dialog__body {
          max-height: 500px;
          overflow-y: auto;
        }
      }
    }
    .el-button--text {
      color: #00a8d7;
    }
    .btns {
      .btn {
        width: 80px;
        height: 36px;
        border: 1px solid #e6e6e6;
        color: #454545;
      }
      .float-r {
        float: right;
      }
    }
    .tables {
      .table {
        border: 0;
        margin: 15px 0;
      }
    }
    .m-t-26 {
      margin-top: 26px;
    }
    .globlgl-type {
      margin-right: 20px;
    }
    .nav {
      color: #99a9bf;
      cursor: pointer;
      transition: .3s;
      vertical-align: baseline;
      margin-right: 30px;
      &:hover {
        color: #48576a;
      }
    }
    .tree-grid {
      margin: 15px 0;
    }
  }

</style>
<style lang="less">
    .globlgl.globlgl--custom .el-table .el-table__body-wrapper {
        overflow: auto;
    }
</style>



