<template>
    <div class="globlgl">
        <div class="btns">
            <el-button class="float-l btn" @click="handleCallFailedFlowAdd">添加</el-button>
            <el-button class="btn" @click="handleBatchDelete">批量删除</el-button>
            <div class="search float-r">
             <el-input class="search-input" placeholder="处理流程ID" icon="search" v-model="searchInput" :on-icon-click="handleCallFailedFlowSearch">
             </el-input>
               
            </div>
        </div>
        <div class="tables">
        <el-table class="table" ref="multipleTable" :data="callFailedFlowData" border tooltip-effect="dark" @selection-change="handleSelectionChange">
            <el-table-column type="selection" width="55"></el-table-column>
            <el-table-column prop="vlanId" label="租户号" width="100">
                <template slot-scope="scope">
                    <el-button @click="handlecallFailedFlowCheck(scope.$index, scope.row)" type="text">{{scope.row.vlanId}}</el-button>
                </template>
            </el-table-column>
            <el-table-column prop="callFailedFlowId" label="处理流程ID" width="100"></el-table-column>
            <el-table-column prop="failedReason" label="失败原因"></el-table-column>
            <el-table-column prop="operatType" label="操作类型" ></el-table-column>
            <el-table-column prop="operatData" label="转接号码" width="100"></el-table-column>
            <el-table-column label="操作" width="120">
                <template slot-scope="scope">
                    <el-button type="text" size="small" @click="handleCallFailedFlowDelete(scope.$index, scope.row)">删除</el-button>
                    <el-button type="text" size="small" @click="handleCallFailedFlowEdit(scope.$index, scope.row)">编辑
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
        <!--添加呼叫失败-->
        <el-dialog title="添加呼叫失败" class="w-500" :visible.sync="dialogFormVisible" @close="handleDialogWithFormClose()" :close-on-click-modal="2==1">
            <el-form :model="callFailedFlowForm" :rules="formRules" labelPosition="left" ref="callFailedFlowForm">
                <el-form-item prop="vlanId" label="租户号" :label-width="formLabelWidth" v-if="this.$store.baseStore.getters.getUserInfo.managerLevel == '999'">
                     <el-select v-model.number="callFailedFlowForm.vlanId" placeholder="请选择">
                        <el-option  v-for="item in allCompanyId" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select> 
                </el-form-item>
                <el-form-item prop="failedReason" label="失败原因" :label-width="formLabelWidth">
                    <el-select v-model="callFailedFlowForm.failedReason" multiple placeholder="请选择" @visible-change="failedReasonChange">
                        <el-option  v-for="item in failedReasonInfo" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select> 
                </el-form-item>
                <el-form-item prop="operatType" label="操作类型" :label-width="formLabelWidth">
                    <el-select v-model="callFailedFlowForm.operatType" placeholder="请选择">
                        <el-option  v-for="item in operatTypeInfo" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select> 
                </el-form-item>
                <el-form-item prop="operatData" label="转接号码" :label-width="formLabelWidth" v-if="this.callFailedFlowForm.operatType == 'transfer'">
                    <el-input v-model="callFailedFlowForm.operatData" :maxlength="128" auto-complete="off"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisible = false">取 消</el-button>
            <el-button type="primary" @click="handleCallFailedFlowSave('callFailedFlowForm')">确 定</el-button>
            </div>
        </el-dialog>
        <!--编辑呼叫失败-->
        <el-dialog title="编辑呼叫失败" class="w-500" :visible.sync="dialogFormEdit" @close="handleDialogWithFormClose()" :close-on-click-modal="2==1">
            <el-form :model="callFailedFlowEditForm" :rules="formRules" labelPosition="left" ref="callFailedFlowEditForm">
                <el-form-item prop="vlanId" label="租户号" :label-width="formLabelWidth" v-if="this.$store.baseStore.getters.getUserInfo.managerLevel == '999'">
                    <el-select v-model.number="callFailedFlowEditForm.vlanId" placeholder="请选择">
                        <el-option  v-for="item in allCompanyId" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select> 
                </el-form-item>
                <el-form-item prop="failedReason" label="失败原因" :label-width="formLabelWidth">
                    <el-select v-model="callFailedFlowEditForm.failedReason" multiple placeholder="请选择" @visible-change="failedReasonEditChange">
                        <el-option  v-for="item in failedReasonInfo" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select> 
                </el-form-item>
                <el-form-item prop="operatType" label="操作类型" :label-width="formLabelWidth">
                    <el-select v-model="callFailedFlowEditForm.operatType" placeholder="请选择">
                        <el-option  v-for="item in operatTypeInfo" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select> 
                </el-form-item>
                <el-form-item prop="operatData" label="转接号码" :label-width="formLabelWidth" v-if="this.callFailedFlowEditForm.operatType == 'transfer'">
                    <el-input v-model="callFailedFlowEditForm.operatData" :maxlength="128" auto-complete="off"></el-input>
                    
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormEdit = false">取 消</el-button>
            <el-button type="primary" @click="handleCallFailedFlowSave('callFailedFlowEditForm')">确 定</el-button>
            </div>
        </el-dialog>
        <!--查看弹窗-->
        <el-dialog title="查看呼叫失败" class="w-500" :visible.sync="dialogFormCheck" :close-on-click-modal="2==1">
            <div class="item">租户号: {{showCheck.vlanId}}</div>
            <div class="item">处理流程ID: {{showCheck.callFailedFlowId}}</div>
            <div class="item">失败原因: {{showCheck.failedReason}}</div>
            <div class="item">操作类型: {{showCheck.operatType}}</div>
            <div class="item">转接号码: {{showCheck.operatData}}</div>
            <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormCheck = false">取 消</el-button>
            <el-button type="primary" @click="dialogFormCheck=false">确 定</el-button>
            </div>
        </el-dialog>
        </div>
        <div class="float-r Pagination" style="text-align: left;margin-top: 10px;">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-size="10" layout="total, prev, pager, next" :total="count">
        </el-pagination>
        </div>
    </div>
</template>

<script>

  import {
    callFailedFlowList,
    savecallFailedFlow,
    deletecallFailedFlowByIds
  }
    from '@/api/getData';
  import ElTableTreeColumn from '../base/table-tree/table-column.js';
  export default {
    data() {
      var checkNumber = (rule, value, callback) => { //必须填而且必须是数字
            if (value == undefined || "" === value) {
                callback(new Error('此处不能为空'));
            }
            if (!Number.isInteger(value)) {
                callback(new Error('请输入数字值'));
            } else {
                callback();
            }
        };
        var checkNull = (rule, value, callback) => { //要么不填，要填必须是数字
            if (value == undefined || "" === value) {
                callback();
            }
            if (!Number.isInteger(value)) {
                callback(new Error('请输入数字值'));
            } else {
                callback();
            }
      };
      return {
        //分页
        currentRow: null,
        limit: 10,
        count: 0,
        currentPage: 1,
        searchInput: '', //搜索框
        callFailedFlowData: [],
        showCheck: {}, //查看对象
        dialogFormCheck: false, //查看弹窗

        operatTypeInfo:[{
          value:'transfer',
          label:'转接',
        },{
          value:'voicemail',
          label:'语音留言',
        }],
        failedReasonInfo:[{
          value:'CALL_CFU',
          label:'无条件呼转'
        },{
          value:'RECOVERY_ON_TIMER_EXPIRE',
          label:'网络不通'
        },{
          value:'NO_USER_RESPONSE,USER_BUSY',
          label:'分机忙'
        },{
          value:'NO_ANSWER',
          label:'无应答'
        },{
          value:'USER_NOT_REGISTERED',
          label:'不在线'
        },{
          value:'NO_ROUTE_DESTINATION',
          label:'无路由'
        },{
          value:'NORMAL_TEMPORARY_FAILURE',
          label:'其他异常'
        }],
        multipleSelection: [],
        //弹窗
        dialogFormVisible: false,
        dialogFormEdit: false, //编辑弹窗
        callFailedFlowForm: {
          vlanId: 0,
          callFailedFlowId: '',
          operatType: '',
          failedReason: [],
          operatData: '',
        },
        callFailedFlowEditForm: {
             vlanId: 0,
             callFailedFlowId: '',
             operatType: '',
             failedReason: [],
             operatData: '',
        },
        formLabelWidth: '80px',
        formRules: {
           vlanId: [{required: true,  validator: checkNumber, message:'请输入租户号(数字)',trigger: 'blur'}],
           callFailedFlowId: [{validator: checkNull, trigger: 'blur'}],
        },
        currentUserVlanId:this.$store.baseStore.getters.getUserInfo.vlanId,

      }
    },
    created() {
      this.getcallFailedFlowList(); //加载数据
    },
    mounted(){
        var coms = this.$store.baseStore.getters.getAllCompanyInfo;
        if(coms == undefined || coms.length == 0){
            this.$store.baseStore.dispatch('getAllDeptId');
        }else{
            this.$data.allCompanyId= coms;
        }
    },
    computed: {
        allCompanyId:function() {
             return this.$store.baseStore.getters.getAllCompanyInfo;
        }
    },
    components: {
      ElTableTreeColumn
    },
    methods: {
      //获取分页 总条数
      async getcallFailedFlowList() {
        try {
          let query = {
            callFailedFlowId: this.searchInput
          }
          //分页
          const res = await callFailedFlowList({
            pageindex:this.currentPage,
            pagesize:this.limit,
            query:JSON.stringify(query)
          });
          if (res.resultCode == '0') {
            this.callFailedFlowData = [];
            //防止后台数据幅值，，前台页面只需要改动此处就好，还有一个好处 就是只选择我需要的数据
            this.showCheck = res.result.data;
            this.count=res.result.total;
            res.result.data.forEach(item => {
              var failedReason = [];
              var flag = true;
              if(item.failedReason != null){
                item.failedReason.split(",").forEach(temp => {
                  if(temp == 'RECOVERY_ON_TIMER_EXPIRE'){
                    failedReason.push('网络不通');
                  } else if(temp == 'NO_USER_RESPONSE' || temp == 'USER_BUSY'){
                    if(flag){
                      failedReason.push('分机忙');
                      flag = false;
                    }
                  } else if(temp == 'NO_ANSWER'){
                    failedReason.push('无应答');
                  } else if(temp == 'CALL_CFU'){
                    failedReason.push('无条件呼转');
                  } else if(temp == 'USER_NOT_REGISTERED'){
                    failedReason.push('不在线');
                  } else if(temp == 'NO_ROUTE_DESTINATION'){
                    failedReason.push('无路由');
                  } else {
                    failedReason.push('其他异常');
                  }
                });
              }
              var operatType = '';
              if(item.operatType == 'voicemail'){
                operatType = '语音留言';
                item.operatData = '无';
              } else if(item.operatType == 'transfer'){
                operatType = '转接';
              }
              const tableItem = {
                callFailedFlowId:item.callFailedFlowId,
                createAt:item.createAt,
                failedReason:failedReason.toString(),
                operatData:item.operatData,
                operatType:operatType,
                updateAt:item.updateAt,
                uuid:item.uuid,
                vlanId:item.vlanId,
              }
              this.callFailedFlowData.push(tableItem)
            })
          } else {
            throw new Error(res.message)
          }
        } catch (err) {
          console.log('获取数据失败', err);
        }
      },
      handSaveParam(args){
          if(args.callFailedFlowId == undefined || args.callFailedFlowId === '')delete args.callFailedFlowId;
          if(args.operatType == 'voicemail') args.operatData = '';
         return args;
      },
      handleSizeChange(val) {
      },
      handleCurrentChange(val) {
        this.currentPage = val;
        this.getcallFailedFlowList();
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      handleCallFailedFlowSearch() {
        this.getcallFailedFlowList();
      },
      formatter(row, column) {
        return ' -- ' + row.name;
      },
      dataFormat(row, column) {
         var time =  row[column.property];
         if(time != undefined && time != ''){
            return this.$moment(time).format("YYYY-MM-DD HH:mm:ss");   
         }else{
            return '';
         }
      },
      //判断失败原因中是否存在无条件呼转，存在则只保留无条件呼转
      failedReasonChange(value){
        var flag = false;
        this.callFailedFlowForm.failedReason.forEach(item => {
          if(item == 'CALL_CFU'){
            flag = true;
          }
        });
        if(flag){
          this.callFailedFlowForm.failedReason = ['CALL_CFU'];
        }
      },
      failedReasonEditChange(value){
        var flag = false;
        this.callFailedFlowEditForm.failedReason.forEach(item => {
          if(item == 'CALL_CFU'){
            flag = true;
          }
        });
        if(flag){
          this.callFailedFlowEditForm.failedReason = ['CALL_CFU'];
        }
      },
      //增加
      handleCallFailedFlowAdd() {
        var tempVlanId = this.$data.currentUserVlanId;
        this.callFailedFlowForm = {
          vlanId: tempVlanId,
          callFailedFlowId: '',
          operatType: '',
          failedReason: ['CALL_CFU'],
          operatData: '',
        };
        this.dialogFormVisible = true;
      },
      //关闭弹框时重置表单
      handleDialogWithFormClose() {
        for (var prop in this.$refs) {
          if (this.$refs.hasOwnProperty(prop)) {
            if (this.$refs[prop].$vnode.tag == 'vue-component-48-ElForm') {
              this.$refs[prop].resetFields();
            }
          }
        }
      },
      //批量删除
      handleBatchDelete() {
        let that = this;
        if (this.multipleSelection.length === 0) {
          this.$alert('请至少选择一条记录', '提示', {
            confirmButtonText: '确定',
            callback: () => {
              return false;
            }
          });
        } else {
          this.$confirm('此操作将永久删除记录, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(async() => {
            try {
              let deletedIdArr = [];
              that.multipleSelection.forEach(s => {
                deletedIdArr.push(s.uuid)
              })
              let deletedIdsString = deletedIdArr.join(',');
              let data = {
                uuid: deletedIdsString
              }
              //此方法可以和后台协商一致，单条与批量删除使用同一个函数
              const result = await deletecallFailedFlowByIds(data);
              if (result.resultCode == '0') {
                that.getcallFailedFlowList();
                this.$message({
                  type: 'success',
                  message: '删除成功!'
                });
              }
            } catch (err) {
              console.log(err)
            }
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '已取消删除'
            });
          });
        }
      },
      //删除当前记录
      handleCallFailedFlowDelete(index, row) {
        let that = this;
        this.$confirm('此操作将永久删除记录, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(async() => {
          try {
            let deletedIdsString = [row.uuid].join(',');
            let data = {
              uuid: deletedIdsString
            }
            //此方法可以和后台协商一致，单条与批量删除使用同一个函数
            const result = await deletecallFailedFlowByIds(data);
            if (result.resultCode == '0') {
              that.getcallFailedFlowList();
              this.$message({
                type: 'success',
                message: '删除成功!'
              });
            }
          } catch (err) {
            console.log(err)
          }
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      },
      //编辑
      handleCallFailedFlowEdit(index, row) {
        var failedReason = [];
        row.failedReason.split(",").forEach(temp => {
          if(temp == '网络不通'){
            failedReason.push('RECOVERY_ON_TIMER_EXPIRE');
          } else if(temp == '分机忙'){
            failedReason.push('NO_USER_RESPONSE,USER_BUSY');
          } else if(temp == '无条件呼转'){
            failedReason.push('CALL_CFU');
          }  else if(temp == '无应答'){
            failedReason.push('NO_ANSWER');
          } else if(temp == '不在线'){
            failedReason.push('USER_NOT_REGISTERED');
          } else if(temp == '无路由'){
            failedReason.push('NO_ROUTE_DESTINATION');
          } else {
            failedReason.push('NORMAL_TEMPORARY_FAILURE');
          }
        });
        var operatType = '';
        if(row.operatType == '语音留言'){
          operatType = 'voicemail';
        } else if(row.operatType == '转接'){
          operatType = 'transfer';
        }
        if (typeof row !== 'undefined') {
            this.callFailedFlowEditForm.vlanId = row.vlanId;
            this.callFailedFlowEditForm.callFailedFlowId = row.callFailedFlowId;
            this.callFailedFlowEditForm.operatType = operatType;
            this.callFailedFlowEditForm.failedReason = failedReason;
            this.callFailedFlowEditForm.operatData = row.operatData;
            this.callFailedFlowEditForm.uuid = row.uuid;
        }
        this.dialogFormEdit = true;
      },
      //查看
      handlecallFailedFlowCheck(index, row) {
        this.dialogFormCheck = true;
        this.$data.showCheck = row;
      },
      //添加 编辑的 保存
      handleCallFailedFlowSave(formName) {
        let that = this;
        this.$refs[formName].validate(async(valid) => {
          if (valid) {
            try {
              //数据拿出来，如果为空则去掉
              var args = this.$refs[formName].model;
              var params = this.handSaveParam(args);
              const result = await savecallFailedFlow(params);
              if (result.resultCode == '0') {
                  if(formName == 'callFailedFlowForm'){//添加
                        that.dialogFormVisible = false;
                  }else{
                    this.dialogFormEdit = false;
                  }
                  that.getcallFailedFlowList();
                  this.$message({
                     type: 'success',
                     message: '呼叫失败保存成功'
                  });
              }else{
                  this.$message({
                    type: 'error',
                    message: '保存失败'
                  });
              }
            } catch (err) {
               this.$message({
                  type: 'error',
                  message: '保存失败'
               });
            }
          } else {
             this.$message({
                type: 'info',
                message: '请检查输入是否不合法'
             });
            return false;
          }
        });
      },
    }
  }

</script>
<style scope lang="less">

  .globlgl {
    padding: 33px 0 0 26px;
    box-sizing: border-box;
    .float-l {
      float: left;
    }
    .float-r {
      float: right;
    }
    .m-t-25 {
      margin-top: 25px;
    }
    //table 改组建
    .el-table {
      width: 100%;
      height: 100%;
      border: 0;
      &:before,
      &:after {
        display: none;
      }
      .el-table__body-wrapper {
        overflow: hidden;
      }
      th,
      td,
      .cell {
        border: 0px;
        background-color: initial;
        padding: 0px;
      }
      .cell {
        padding-left: 10px;
      }
      tr {
        border-bottom: 1px solid #e6e6e6;
      }
      thead tr {
        border-bottom: 2px solid #e6e6e6;
      }
    }
    .w-500 {
      .el-dialog {
        width: 500px;
        .el-dialog__body {
          max-height: 500px;
          overflow-y: auto;
          .item{
            height:40px;
            line-height:40px;
          }
        }
      }
    }
    .w-1000 {
      .el-dialog {
        width: 1000px;
        .el-dialog__body {
          max-height: 500px;
          overflow-y: auto;
        }
      }
    }
    .el-button--text {
      color: #00a8d7;
    }
    .btns {
      .btn {
        width: 80px;
        height: 36px;
        border: 1px solid #e6e6e6;
        color: #454545;
      }
      .float-r {
        float: right;
      }
    }
    .tables {
      .table {
        border: 0;
        margin: 15px 0;
      }
    }
    .m-t-26 {
      margin-top: 26px;
    }
    .globlgl-type {
      margin-right: 20px;
    }
    .nav {
      color: #99a9bf;
      cursor: pointer;
      transition: .3s;
      vertical-align: baseline;
      margin-right: 30px;
      &:hover {
        color: #48576a;
      }
    }
    .tree-grid {
      margin: 15px 0;
    }
  }

</style>


