<template>
    <div class="tongji-wrapper">
        <div class="menu tac">
            <el-row>
                <el-col>
                    <el-menu :default-active="defaultMenu" @select="openMenu">
                        <el-submenu index="call" v-show="callmenu">
                            <template slot="title" class="el-menu-title"><i class="tongji-nav-arrow el-icon-caret-right c4c4c4"></i><span>呼叫管理</span></template>                            
                            <el-menu-item index="callFailedFlow" v-show="callFailedFlowmenu">呼叫失败</el-menu-item>
                            <el-menu-item index="callcontrol" v-show="callcontrolmenu">外呼限制管理</el-menu-item>
                        </el-submenu>
                        <el-submenu index="record" v-show="recordmenu">
                            <template slot="title" class="el-menu-title"><i class="tongji-nav-arrow el-icon-caret-right c4c4c4"></i><span>录音管理</span></template>                            
                            <el-menu-item index="recordConf" v-show="recordConfmenu">录音配置</el-menu-item>
                            <el-menu-item index="recordCdrs" v-show="recordCdrsmenu">录音记录</el-menu-item>
                        </el-submenu>
                    </el-menu>
                </el-col>
            </el-row>
        </div>
        <div class="main">
            <callcontrol v-if="selectMenuName=='callcontrol'"></callcontrol>
            <callFailedFlow v-else-if="selectMenuName=='callFailedFlow'"></callFailedFlow>
            <recordConf v-else-if="selectMenuName=='recordConf'"></recordConf>
            <recordCdrs v-else-if="selectMenuName=='recordCdrs'"></recordCdrs>
        </div>
    </div>
</template>

<script>
  import callcontrol from "./callcontrol.vue"
  import callFailedFlow from './callFailedFlow.vue'
  import recordConf from './recordConf.vue'
  import recordCdrs from './recordCdrs.vue'
export default {
    components: {
       callcontrol:callcontrol,
       callFailedFlow:callFailedFlow,
       recordConf:recordConf,
       recordCdrs:recordCdrs,
    },
    data() {
        return {
            selectMenuName: '',
            defaultMenu: '',

            callmenu: false,
            callFailedFlowmenu: false,
            blackListmenu: false,
            callerBlackListsmenu: false,
            calleeBlackListsmenu: false,
            callcontrolmenu: false,
            recordmenu: false,
            recordConfmenu: false,
            recordCdrsmenu : false,

        }
    },
    created(){

        let userInfo = this.$store.baseStore.getters.getUserInfo;
        let secondmenu = userInfo.menu.secondmenuset;
        if(secondmenu.indexOf("录音管理") != -1){
             this.$data.recordmenu = true
        }
        if(secondmenu.indexOf("呼叫管理") != -1){
             this.$data.callmenu = true
        }

        let thirdmenu = userInfo.menu.thirdmenuset;
        let defaultmenu = "";
        if(thirdmenu.indexOf("呼叫失败") != -1){
             this.$data.callFailedFlowmenu = true
             defaultmenu = "callFailedFlow";
        }
        
        if(thirdmenu.indexOf("外呼限制管理") != -1){
             this.$data.callcontrolmenu = true
             if(defaultmenu == ""){
                  defaultmenu = "callcontrol";
             }
        }
        if(thirdmenu.indexOf("录音配置") != -1){
             this.$data.recordConfmenu   = true
             if(defaultmenu == ""){
                  defaultmenu = "recordConf";
             }
        }
        if(thirdmenu.indexOf("录音记录") != -1){
             this.$data.recordCdrsmenu = true
             if(defaultmenu == ""){
                  defaultmenu = "recordCdrs";
             }
        }
        
        this.$data.defaultMenu = defaultmenu;
        this.$data.selectMenuName =  defaultmenu;

        var currentSelectMenu = this.$store.baseStore.getters.getCurrentSelectMenu;
        if(currentSelectMenu != undefined && currentSelectMenu.secondDegreMenu != undefined
            && currentSelectMenu.secondDegreMenu != '' && currentSelectMenu.firstDegreeMenu == 'call'){
            this.$data.selectMenuName = currentSelectMenu.secondDegreMenu;
            this.$data.defaultMenu = currentSelectMenu.secondDegreMenu;
        }else{
            var menu = new Object();
            menu['secondDegreMenu'] = defaultmenu;
            menu['firstDegreeMenu'] = 'call';
            this.$store.baseStore.commit('setCurrentSelectMenu',menu);
        }
    },
    methods: {
        /**
         * 打开菜单
         */
        openMenu: function(index, indexPath) {
            this.$data.selectMenuName = index;
            var menu = new Object();
            menu['secondDegreMenu'] = index;
            menu['firstDegreeMenu'] = 'call';
            this.$store.baseStore.commit('setCurrentSelectMenu',menu);
        },

        // resize 重置菜单和内容高度
        handleWindowResize() {
          const selector = (selectorName) => {
            return document.querySelector(selectorName);
          };
          const height = window.innerHeight - 50;
          
          selector('.main').style.height = height + 'px';
          selector('.menu.tac').style.height = height + 'px';
          selector('.tongji-wrapper').style.height = height + 'px';
        }, 
    },
    mounted() {
        this.handleWindowResize();
        window.addEventListener('resize', this.handleWindowResize);
        var menu = new Object();
        menu['secondDegreMenu'] = 'cdrs';
        menu['firstDegreeMenu'] = 'call';
        this.$store.baseStore.commit('setCurrentSelectMenu',menu);
    },
    beforeDestroy () {
        window.removeEventListener('resize', this.handleWindowResize);
    },
}
</script>

<style lang="less">
  @import "../../common/style/imCommon.less";

  .tongji-wrapper {
    #common-wrapper();
    
    position: relative!important;
    & ::-webkit-scrollbar {
      width: 5px;
      background: transparent;
    }
  }

  .tongji-wrapper ::-webkit-scrollbar-track-piece {
    background: rgba(0, 0, 0, 0.2);
  }

  .tongji-wrapper ::-webkit-scrollbar-thumb {
    height: 50px;
    border: none;
    border-radius: 6px;
    background-clip: padding-box;
    background-color: #959595;
    box-shadow: none;
  }

  .tongji-wrapper {
    display:table-cell;
    position:initial;
    width:100%;
    height:calc(~"100% - 48px");
    float:left;
    color:#3e3e3e;
    font-size:14px;
    .tac{
      width:217px;
      border-right:1px solid #e6e6e6;
      background-color:#f4f6f6;
      float:left;
      overflow: hidden;
      .el-menu{
        background:#f4f6f6;
        overflow-y: auto;
      }
      .el-menu-item{
        &:hover{
          background:#ebfbff;
          color:#00a8d7;
          border-right:1px solid #00a8d7;
        }
        &.is-active{
          background:#ebfbff;
          color:#00a8d7;
          border-right:2px solid #00a8d7;
        }
      }
      .el-submenu__icon-arrow {
        &.el-icon-arrow-down{
          &:before{
            display: none;
          }
        }
      }
      .el-submenu {
        .el-submenu__title{
          height:50px;
          line-height:50px;
          margin-left:-6px;
        }
        .el-menu-item-group{
          background:#f4f6f6;
        }
        .el-menu-item{
          height:40px;
          line-height:40px;
        }
      }
      .el-menu-item-group__title{
        margin-top:-15px;
      }
      .el-menu-title{
        &:hover{
          background:#f00;
        }
      }
      //menu
      .el-submenu__title{
        margin-left:-6px;
      }
      .c4c4c4{
        color:#c4c4c4;
        font-size:12px;
      }
    }
    &>.main {
        width: calc(~"100% - 220px");
        height: 100%;
        float: left;
        .component-wrapper {
            position: relative;
            font-size: 14px;
            color: #3e3e3e;
            padding: 15px 30px;
            &>.component-header {
               .title {
                    font-size: 16px;
                    font-weight:bold;
                }
            }
            &>.component-main {
                padding-top: 15px;
                .search-wrapper {
                    &>.search-txt {
                        width: 190px;
                    }
                    &>.el-select {
                        width: 160px;
                    }
                }
                .cardSection-wrapper {
                    .item {
                        width: 100%;
                        border: 1px solid #e6e6e6;
                        border-radius: 3px;
                        .header {
                            height: 36px;
                            font-size: 14px;
                            color: #3e3e3e;
                            font-weight: bold;
                            background-color: #f4f6f6;
                            border-bottom: 1px solid #e6e6e6;
                            text-align: center;
                            line-height: 35px;
                        }
                    }
                }
                .table-wrapper {
                    &>.header {
                        font-size: 14px;
                        color: #3e3e3e;
                    }
                    &>.el-pagination {
                        padding: 0px;
                        padding-top: 25px;
                        text-align: right;
                    }
                }
            }
        }
    }
  }
  .chart-no-data {
    min-height: 150px;
    position: relative;

    .no-data-text {
      position: absolute;
      top: 50%;
      left: 50%;
      color: #6e6e6e;
      transform: translate(-50%, -50%);
    }
  }
  .el-submenu {
    .el-submenu__title {
      span {
        margin-left: 10px;
      }
    }
    .tongji-nav-arrow[class^=el-icon-] {
      margin: 0;
    }
  }
  .el-submenu.is-opened>.el-submenu__title .tongji-nav-arrow.el-icon-caret-right {
    transform: rotate(90deg);
  }
  .tongji-wrapper .search-wrapper > * {
    vertical-align: middle;
    &.search-txt {
      .el-input__icon.el-icon-search {
        left: 0;
      }
      .el-input__inner {
        padding-right: 0;
        padding-left: 35px;
      }
    }
    &.el-button {
      margin-left: 0;
      &.float-right {
        margin-left: 10px;
      }
    }
  }
</style>
<style scoped="scoped" lang="less">
  .main, .menu.tac {
    padding-bottom: 10px;
    overflow: auto;
    box-sizing: border-box;
  }
</style>



